let atras = document.getElementById('atras');
let adelante = document.getElementById('adelante');
let imagen = document.getElementById('img');
let puntos = document.getElementById('puntos');
let texto = document.getElementById('texto');
let masInformacionIcono = document.querySelectorAll('#masInfoEstudio');
let masInformacion = document.querySelectorAll('#masInformacion');
let estudio = document.querySelectorAll('#estudio')
let antes = imagenes.length - 1
posicionCarrusel()


atras.addEventListener('click', function () {

    antes -= 1

    if (antes == -1) {
        antes = imagenes.length - 1
    }

    imagen.innerHTML = ""
    texto.innerHTML = ""
    imagen.innerHTML += `
    <img class="img" src="${imagenes[antes].url}" alt="logo pagina" loading="lazy"></img>
    `;
    texto.innerHTML = `
    <h3>${imagenes[antes].nombre}</h3>
    <p>${imagenes[antes].descripcion}</p>`
    posicionCarrusel()
})

adelante.addEventListener('click', function () {

    antes += 1

    if (antes == imagenes.length) {
        antes = 0
    }

    imagen.innerHTML = "";
    texto.innerHTML = "";
    imagen.innerHTML += `
    <img class="img" src="${imagenes[antes].url}" alt="logo pagina" loading="lazy"></img>    `;
    texto.innerHTML = `
    <h3>${imagenes[antes].nombre}</h3>
    <p>${imagenes[antes].descripcion}</p>`

    posicionCarrusel()

})

function posicionCarrusel() {
    puntos.innerHTML = ""
    for (var i = 0; i < imagenes.length; i++) {
        if (i == antes) {
            puntos.innerHTML += '<p class="bold">.<p>'
        }
        else {
            puntos.innerHTML += '<p>.<p>'
        }
    }
}


// Animaciones Carrusel
let oculto = true

for (let i = 0; i < masInformacionIcono.length; i++){
    masInformacionIcono[i].addEventListener('click', function() {
        if (oculto == true){
            masInformacion[i].classList.remove("ocultar");
            masInformacion[i].classList.add('mostrar')
            oculto = false
        }
        else{
            masInformacion[i].classList.remove('mostrar')
            masInformacion[i].classList.add('ocultar')
            oculto = true 
        }
    
    })
}


let desplegar = false
for (let i = 0; i < masInformacionIcono.length; i++){

    masInformacionIcono[i].addEventListener('click', function(){
        console.log(estudio)
        if (desplegar == false){
            estudio[i].classList.add('desplegado')
            desplegar = true
        }
        else{
            estudio[i].classList.remove('desplegado')
            desplegar = false 
        }
    })
    
}

